@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../../fonts/alumnisans-semibold.woff2") format("woff2"), url("../../fonts/alumnisans-semibold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../../fonts/robotocondensed-light.woff2") format("woff2"), url("../../fonts/robotocondensed-light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../../fonts/robotocondensed-regular.woff2") format("woff2"), url("../../fonts/robotocondensed-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../../fonts/robotocondensed-bold.woff2") format("woff2"), url("../../fonts/robotocondensed-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0;
  }
}

.header {
  position: relative;
}

.header__wrapper {
  margin: 0 auto;
  max-width: 1920px;
  width: 100%;
  position: absolute;
  z-index: 2;
  top: 0;
  left: 0;
  right: 0;
  padding: 30px 103px 0;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1199px) {
  .header__wrapper {
    padding: 40px 45px 0;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    padding: 15px 15px 0;
  }
}

.header__logo-link {
  outline: none;
}
.header__logo-link:hover, .header__logo-link:focus {
  opacity: 0.7;
}
.header__logo-link:active {
  opacity: 0.5;
}

.header__logo-link svg {
  width: 122px;
  height: 44px;
}
@media (max-width: 1199px) {
  .header__logo-link svg {
    width: 82px;
    height: 17px;
  }
}
@media (max-width: 767px) {
  .header__logo-link svg {
    width: 69px;
    height: 24px;
  }
}

.header__phone a {
  font-size: 26px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 0.78px;
  color: #ffffff;
  outline: none;
}
.header__phone a:hover, .header__phone a:focus {
  opacity: 0.7;
}
.header__phone a:active {
  opacity: 0.5;
}
@media (max-width: 1199px) {
  .header__phone a {
    font-size: 24px;
    font-weight: 700;
    letter-spacing: 0.48px;
  }
}
@media (max-width: 767px) {
  .header__phone a {
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 0.4px;
  }
}

.footer {
  background-color: #030a1a;
}

.footer__wrapper {
  width: 100%;
  margin: 0 auto;
  padding: 40px 0 30px;
  color: #ffffff;
  display: grid;
  grid-template-columns: 16% auto 16%;
}
@media (max-width: 1199px) {
  .footer__wrapper {
    padding: 41px 0 40px;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    display: grid;
    grid-template-columns: 100%;
    row-gap: 40px;
    align-items: start;
    padding: 50px 15px 52px;
  }
}

.footer__logo {
  display: flex;
}
@media (max-width: 767px) {
  .footer__logo {
    grid-row: 1/3;
    grid-column: 1/2;
  }
}

.footer__logo-link {
  width: 100%;
  display: block;
  outline: none;
}
.footer__logo-link:hover, .footer__logo-link:focus {
  opacity: 0.6;
}
.footer__logo-link:active {
  opacity: 0.5;
}
@media (max-width: 767px) {
  .footer__logo-link {
    width: 50%;
  }
}

.footer__logo-link svg {
  vertical-align: middle;
  width: 88px;
  height: 32px;
}
@media (max-width: 767px) {
  .footer__logo-link svg {
    width: 68px;
    height: 15px;
  }
}

.nav {
  display: flex;
  align-items: center;
}
@media (max-width: 767px) {
  .nav {
    grid-row: 1/3;
    grid-column: 1/2;
  }
}

.nav__list {
  display: grid;
  grid-template-columns: repeat(5, auto);
  gap: 20px;
  grid-auto-flow: row;
  justify-content: center;
  color: #ffffff;
  width: 100%;
}
@media (max-width: 1199px) {
  .nav__list {
    gap: 8px;
  }
}
@media (max-width: 767px) {
  .nav__list {
    justify-content: start;
    display: grid;
    grid-template-columns: repeat(2, 50%);
    grid-template-rows: repeat(3, auto);
    grid-auto-flow: column;
    column-gap: 0;
  }
}

.nav__item {
  width: 100%;
}
@media (max-width: 767px) {
  .nav__item:first-child {
    grid-row-start: 2;
  }
}

.nav__item a {
  padding: 8px 10px;
  text-align: center;
  display: block;
  font-size: 14px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  color: #ffffff;
  outline: none;
}
.nav__item a:hover {
  border-bottom: 1px solid #ffffff;
}
.nav__item a:focus {
  border: 1px solid #ffffff;
}
.nav__item a:active {
  border: none;
}
.nav__item a:disabled {
  opacity: 0.4;
}
@media (max-width: 767px) {
  .nav__item a {
    text-align: start;
    font-size: 16px;
    padding: 8px 10px 8px 0;
  }
}

.social {
  display: flex;
  align-items: center;
}

.social__list {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-auto-flow: row;
  gap: 11px;
  justify-content: end;
}
@media (max-width: 767px) {
  .social__list {
    justify-content: start;
  }
}

.social__item {
  width: 25px;
  height: 25px;
  justify-content: center;
  align-items: center;
  position: relative;
  outline: none;
}

.social__link svg {
  position: absolute;
  top: 9px;
  left: 6px;
  z-index: 2;
  fill: #030a1a;
}

.social__link--ok svg {
  top: 4px;
  left: 7.5px;
}

.social__link--reddit svg {
  top: 5px;
  left: 4.5px;
}

.social__link {
  display: block;
  width: 25px;
  height: 25px;
  outline: none;
}

.social__link::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 25px;
  height: 25px;
  border-radius: 25px;
  background-color: #ffffff;
  cursor: pointer;
}

.social__link:hover::after,
.social__link:focus::after {
  background-color: #103aae;
}

.social__link:hover svg,
.social__link:focus svg,
.social__link:active svg {
  fill: #ffffff;
}

.social__link:active::after {
  background-color: #102654;
}

.social__link:disabled {
  opacity: 0.4;
}

.hero {
  background: conic-gradient(from 181deg at 16.03% 39.06%, #003D85 12deg, #B80027 263deg, #003D85 333deg);
  width: 100%;
}
@media (max-width: 1199px) {
  .hero {
    background: conic-gradient(from 175deg at 20.7% 35.74%, #003D85 12deg, #B80027 280deg, #003D85 360deg);
  }
}
@media (max-width: 767px) {
  .hero {
    background: conic-gradient(from 210deg at 35.31% 34.22%, #003D85 12deg, #B80027 283deg, #003D85 360deg);
  }
}

.hero__wrapper {
  width: 90%;
  display: grid;
  grid-template-columns: 15% auto 68%;
  column-gap: 24px;
  grid-template-rows: 1fr 15%;
  padding: 51px 0;
}
@media (max-width: 1199px) {
  .hero__wrapper {
    width: 98%;
    padding: 82px 0;
    grid-template-columns: 17% auto 47%;
  }
}
@media (max-width: 767px) {
  .hero__wrapper {
    width: 100%;
    grid-template-columns: 30% 70%;
    column-gap: 0;
    padding: 82px 15px;
  }
}

.hero__list {
  display: flex;
  flex-direction: column;
  text-align: right;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  color: #ffffff;
  align-self: end;
  margin-bottom: 115px;
  padding: 0 10px 0 0;
}
@media (max-width: 767px) {
  .hero__list {
    margin-bottom: 100px;
    position: absolute;
  }
}

.hero__beginning {
  display: flex;
  flex-direction: column-reverse;
}
@media (max-width: 767px) {
  .hero__beginning {
    justify-self: end;
    grid-column: 2/3;
  }
}

.hero__beginning h1 {
  text-align: right;
  text-transform: uppercase;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 92px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 4.6px;
  color: #ffffff;
  -webkit-text-stroke-width: 2px;
  margin: 0 0 80px 0;
}
@media (max-width: 1199px) {
  .hero__beginning h1 {
    font-size: 72px;
    letter-spacing: 3.6px;
  }
}
@media (max-width: 767px) {
  .hero__beginning h1 {
    margin: 0 0 20px 0;
  }
}

.hero__beginning h2 {
  text-align: right;
  text-transform: uppercase;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 48px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 1.44px;
  color: #ffffff;
  margin: 0 0 25px 0;
  transform: translateX(-18%);
}
@media (max-width: 1199px) {
  .hero__beginning h2 {
    font-size: 34px;
    letter-spacing: 1.02px;
    transform: translateX(0);
  }
}
@media (max-width: 767px) {
  .hero__beginning h2 {
    font-size: 28px;
    letter-spacing: 0.84px;
    margin-top: 15px;
  }
}

.hero__button {
  max-width: 230px;
  max-height: 57px;
  grid-column: 2;
  justify-self: end;
}
@media (max-width: 767px) {
  .hero__button {
    grid-column: 1/3;
    justify-self: center;
    max-width: 100%;
  }
}

.hero__button a {
  display: block;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  border: 4px solid #ffffff;
  padding: 17px 52.5px 18px;
  outline: none;
  cursor: pointer;
}
.hero__button a:hover, .hero__button a:focus {
  color: #103aae;
  background-color: #ffffff;
}
.hero__button a:active {
  color: #102654;
  background-color: #ffffff;
}
.hero__button a:disabled {
  opacity: 0.5;
}
@media (max-width: 1199px) {
  .hero__button a {
    padding: 17px 29px 18px;
  }
}
@media (max-width: 767px) {
  .hero__button a {
    padding: 18px 82.5px 19px;
  }
}

.hero__img {
  background-color: #003D85;
